import React, { Component } from 'react'

export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="timeline">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">MY WORK</span>
                <h2 className="colorlib-heading animate-box">Timeline</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-3">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Full-Stack Developer <span>Aug-2020 to present</span></h2>
                        <p>Working with data reconnoissance project which compares live Kafka data with user files. We utilise multiple micro-services to process and generate reports on hourly, daily and weekly basis. Spring boot, Apache Kafka, Ignite and MongoDb, Docker, Openshift/AWS, Angular 8, etc are building blocks for this project.</p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-4">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Full-Stack Developer <span>Oct-2019 to Aug-2020</span></h2>
                        <p>Upgrading a legacy Banking application written in DotNet to Java environment. The backend services are supported by Spring Boot, Hibernate and micro services using Pivotal Cloud Foundry. Secutity is taken care by Lightweight Directory Access Protocol (LDAP) and server to server authentication is maintained by JSON Web Token (JWT). Angular 8 is used as frontend framework in this project.</p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Java Backend Developer <span>June-2019 to Oct-2019</span></h2>
                        <p>Application developed to Extract Transform and Load (ETL tool) specific files according to the client requirements. utilized batch processing tool like Spring Batch to process large chunks of data simultaniously. Developed pipelines to streamline data from different sources.</p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
